.offers {
  padding: 125px 0 42px 0;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 44px;
    line-height: 62px;
    text-align: center;
    color: var(--title-color);
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 109px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: var(--text-dark-color);
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 35px;
  }

  &__item {
    padding: 40px;
    overflow: hidden;
    background: var(--light-color);
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
  }

  &__item-title {
    margin: 0;
    margin-bottom: 32px;
    display: flex;
    align-items: center;
    font-size: 28px;
    line-height: 31px;
    color: var(--title-dark-color);

    & img {
      margin-right: 10px;
    }
  }

  &__item-descr {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
  }
}
